"use client"
import { useState } from "react"
import { createTodo } from "@/actions/todo.action";

export default function CreateTodo() {
    const [text, setText] = useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            createTodo(text);
            setText("");
        } catch (e) {
            console.log(e);
        }
    };

    return (
        <div>
            <div>Create Todo</div>
                <form onSubmit={handleSubmit}>
                    <label htmlFor="create-todo">
                        <input
                            value = {text}
                            onChange = {(e) => setText(e.target.value)}
                            type="text"
                            name = "todo-text"
                            id = "todo-text"
                            className="formInput"
                        />
                    </label>
                    <button className="formButton">Create</button>
                </form>

        </div>
    );
}



