'use server';
import { connectToDB } from "@/database";
import mongoose from "mongoose";
import { revalidatePath } from "next/cache";
import TodoModel from "@/modals/todo.modal";
import { auth } from "@clerk/nextjs/server"; 
import { ObjectId } from "mongodb";

/**
 * Get all todos
 * @returns 
 */
export async function getTodos() {
    try {
        const db = await connectToDB();
        const { userId } = await auth();
        const todos = await TodoModel.find({ clerkId: userId }).lean();

        const cleanTodos = todos.map(todo => ({
            ...todo,
            _id: todo._id.toString(),
        }));

        return cleanTodos;
    } catch (e) {
        console.log(e);
    }
}

export async function createTodo(text){
    const { userId } = await auth();
    const todo = await TodoModel.create({clerkId: userId, message: text});
    console.log(todo);
    revalidatePath("/");
}

export async function deleteTodo(id){
    await TodoModel.findOneAndDelete({_id: ObjectId.createFromHexString()})
}